import React, { useEffect, useRef, useState,lazy,Suspense } from 'react'
import MiniPlayer from './Comps/MiniPlayer'
import music_data from "./Comps/music_data"
const MusicListDisplayer = lazy(()=>import('./Comps/MusicListDisplayer'))
const log=console.log

const App = () => {
    const [song_arr,setSongArr]=useState(music_data)
    const [index,setIndex]=useState(0)
    const [is_playing,setIsPlaying]=useState(false)
    const [isMini,setMini]=useState(true)
    const [display_mini,setDisplayMini]=useState(false)
    const [progress_line,setProgressLine]=useState(0)
    const [time,setTime]=useState({current_time:'',total_time:''})
    const [is_fetching,setIsFetching]=useState(false)

    const audio_element=useRef(null)
    const progress_bar_element=useRef(null)

    useEffect(()=>{
        if(is_playing)
            audio_element.current.play()
        else
            audio_element.current.pause()
    },[is_playing,index])


    function playThisSong(song_id){
        setIndex(song_id)
        setDisplayMini(true)
        setIsPlaying(true)
    }


    function pauseMusic(){
        setIsPlaying(!is_playing)
    }
    function setExpand(){
        setMini(false)
    }
    function setShrink(){
        setMini(true)
    }


    function nextMusic(){
        const current_index  = (index+1)%song_arr.length
        setIndex(current_index)
    }
    function prevMusic(){
        const current_index=(index-1+song_arr.length)%song_arr.length
        setIndex(current_index)
    }
    
    

    function setProgressBar(e){
        const {currentTime,duration}=e.target
        let percent = (currentTime/duration)*100

        setProgressLine(percent)


        let current_min = Math.floor(audio_element.current.currentTime/60)
        let current_sec = Math.floor(audio_element.current.currentTime%60)
        if(current_sec<10) current_sec = "0"+current_sec
         
        let total_min = Math.floor(audio_element.current.duration/60)
        let total_sec = Math.floor(audio_element.current.duration%60)
        if(total_sec<10) total_sec = "0"+total_sec

        if(total_min && total_sec) setTime({current_time:current_min+":"+current_sec,total_time:total_min+":"+total_sec})
        
    }

    function jumpSongTime(e){
        const {offsetX} = e.nativeEvent
        const {clientWidth} = e.target

        const {duration}=audio_element.current

        let jump_time = (offsetX/clientWidth)*duration
        audio_element.current.currentTime = jump_time
    }


    function displayLoader(){
        setIsFetching(true)
    }
    function hideLoader(){
        setIsFetching(false)
    }


    return (
        <main>
           <audio src={song_arr[index].src}  ref={audio_element} onTimeUpdate={setProgressBar} onCanPlay={hideLoader} onLoadStart={displayLoader}></audio>

           <Suspense fallback={<div class="loader">
                    <div class="c c1"></div>
                    <div class="c c2"></div>
                </div>}>
                <MusicListDisplayer playThisSong={playThisSong} />
           </Suspense>


               {
                   display_mini ?
                            <MiniPlayer music={music_data[index]} isMini={isMini} setExpand={setExpand} setShrink={setShrink} pauseMusic={pauseMusic} is_playing={is_playing} nextMusic={nextMusic} prevMusic={prevMusic} setProgressBar={setProgressBar} progress_line={progress_line} progress_bar_element={progress_bar_element} jumpSongTime={jumpSongTime} time={time} is_fetching={is_fetching}/> 
                            : null
               }
        </main>
    )
}

export default App
