import React, { useRef, useState} from 'react'

const MiniPlayer = ({music,isMini,setExpand,setShrink,pauseMusic,is_playing,nextMusic,prevMusic,progress_line,progress_bar_element,jumpSongTime,time,is_fetching}) => {
    if(isMini){
        return (
            <section className='mini_player mini_player_active' >
                            <span className='back_btn'></span>
    
                            <div className='details' onClick={setExpand}>
                                <div className="img">   
                                        {
                                            is_fetching ? (<div className="loaders">
                                                                <div className="song_loader c1"></div>
                                                                <div className="song_loader c2"></div>
                                                            </div>)
                                                            :
                                                            <img  src={music.img} alt=""/>
                                        }
                                </div>
                                <h2 className='song_name'>{music.name}</h2>
                                <h3 className='artist_name'>{music.artist}</h3>
    
                                <span className='control'>
                                    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="volume-down" className="svg-inline--fa fa-volume-down fa-w-12" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path fill="currentColor" d="M215.03 72.04L126.06 161H24c-13.26 0-24 10.74-24 24v144c0 13.25 10.74 24 24 24h102.06l88.97 88.95c15.03 15.03 40.97 4.47 40.97-16.97V89.02c0-21.47-25.96-31.98-40.97-16.98zm123.2 108.08c-11.58-6.33-26.19-2.16-32.61 9.45-6.39 11.61-2.16 26.2 9.45 32.61C327.98 229.28 336 242.62 336 257c0 14.38-8.02 27.72-20.92 34.81-11.61 6.41-15.84 21-9.45 32.61 6.43 11.66 21.05 15.8 32.61 9.45 28.23-15.55 45.77-45 45.77-76.88s-17.54-61.32-45.78-76.87z"></path></svg>
                                </span>
                            </div>
    
    
                            <div className="bar_container" ref={progress_bar_element} onClick={jumpSongTime}>
                                <div className="virtual_bar"></div>
                                <div className="progress_bar"  style={{width:progress_line+"%"}}></div>
                            </div>
    
    
                            <div className="timings">
                                <p className="curr_time"></p>
                                <p className="end_time"></p>
                            </div>
    
    
                            <div className="btns">
                                <span className="prev_btn"><i className="fas fa-backward"></i></span>
                                
                                <span className='pause_btn' onClick={pauseMusic}> 
                                    {changePauseBtn(is_playing)}
                                </span>

    
                                <span className="next_btn"><i className="fas fa-forward"></i></span>
                            </div>     
                    </section> 
        )
    }
    else{
        return (
            <section className='mini_player mini_player_active expand_mini_player'>
                            <span className='back_btn' onClick={setShrink}></span>
    
                            <div className='details'>
                                <div className="img">   
                                    {
                                        is_fetching ? (<div className="loaders">
                                                            <div className="song_loader c1"></div>
                                                            <div className="song_loader c2"></div>
                                                        </div>)
                                                        :
                                                        <img  src={music.img} alt=""/>
                                    }
                                </div>
                                <h2 className='song_name'>{music.name}</h2>
                                <h3 className='artist_name'>{music.artist}</h3>
    
                                <span className='control'>
                                    <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="volume-down" className="svg-inline--fa fa-volume-down fa-w-12" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512"><path fill="currentColor" d="M215.03 72.04L126.06 161H24c-13.26 0-24 10.74-24 24v144c0 13.25 10.74 24 24 24h102.06l88.97 88.95c15.03 15.03 40.97 4.47 40.97-16.97V89.02c0-21.47-25.96-31.98-40.97-16.98zm123.2 108.08c-11.58-6.33-26.19-2.16-32.61 9.45-6.39 11.61-2.16 26.2 9.45 32.61C327.98 229.28 336 242.62 336 257c0 14.38-8.02 27.72-20.92 34.81-11.61 6.41-15.84 21-9.45 32.61 6.43 11.66 21.05 15.8 32.61 9.45 28.23-15.55 45.77-45 45.77-76.88s-17.54-61.32-45.78-76.87z"></path></svg>
                                </span>
                            </div>
    
    
                            <div className="bar_container" ref={progress_bar_element} onClick={jumpSongTime}>
                                <div className="virtual_bar"></div>
                                <div className="progress_bar"  style={{width:progress_line+"%"}}></div>
                            </div>
    
    
                            <div className="timings">
                                <p className="curr_time">{time.current_time}</p>
                                <p className="end_time">{time?.total_time}</p>
                            </div>
    
    
                            <div className="btns">
                                <span className="prev_btn" onClick={prevMusic}><i className="fas fa-backward"></i></span>
                                
                                <span className='pause_btn' onClick={pauseMusic}>
                                    {changePauseBtn(is_playing)}
                                </span>
    
                                <span className="next_btn"  onClick={nextMusic}><i className="fas fa-forward"></i></span>
                            </div>     
                    </section> 
        )
    }
    

    
}

export default MiniPlayer


function changePauseBtn(is_playing){   
    return(
        is_playing ?
        <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="pause-circle" className="svg-inline--fa fa-pause-circle fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M256 8C119 8 8 119 8 256s111 248 248 248 248-111 248-248S393 8 256 8zm0 448c-110.5 0-200-89.5-200-200S145.5 56 256 56s200 89.5 200 200-89.5 200-200 200zm96-280v160c0 8.8-7.2 16-16 16h-48c-8.8 0-16-7.2-16-16V176c0-8.8 7.2-16 16-16h48c8.8 0 16 7.2 16 16zm-112 0v160c0 8.8-7.2 16-16 16h-48c-8.8 0-16-7.2-16-16V176c0-8.8 7.2-16 16-16h48c8.8 0 16 7.2 16 16z"></path></svg>
        : 
        <svg aria-hidden="true" focusable="false" data-prefix="fas" data-icon="play-circle" className="svg-inline--fa fa-play-circle fa-w-16" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512"><path fill="currentColor" d="M256 8C119 8 8 119 8 256s111 248 248 248 248-111 248-248S393 8 256 8zm115.7 272l-176 101c-15.8 8.8-35.7-2.5-35.7-21V152c0-18.4 19.8-29.8 35.7-21l176 107c16.4 9.2 16.4 32.9 0 42z"></path></svg>
        
        ) 
    }